.bottomSwiper {
    width: 100%;
    height: 100%;
}

.app__articlesBottom {
    display: flex;
    position: relative;
    height: 300px;
    height: 100%;
    flex-wrap: wrap;
}

.app__articlesBottom-slider {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    flex-direction: column;
    min-width: 275px;
    height: 350px;
    margin: .8rem;
}

.app__articlesBottom-image {
    width: 100%;
    height: 100%;
    height: 65%;
}

.app__articlesBottom-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.app__articlesBottom-content {
    width: 100%;
    height: 100%;
    color: var(--color-black);
}

.app__articlesBottom-content h2 {
    border-bottom: 2px solid gray;
    padding-bottom: .5rem;
}

.app__articlesBottom-content a {
    color: var(--color-black);
    text-decoration: none;
    font-size: .8rem;
    padding: .5rem 0;
}

.app__articlesBottom-content a.isActive {
    color: #e9531e;
  }
.app__articlesBottom-content a:hover {
    color: var(--color-golden);
}

.app__articlesBottom-content p {
    max-height: 68px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 11;
    line-clamp: 11;
    -webkit-box-orient: vertical;
    margin: .5rem 0;
}