.app__articlesSwiper {
    max-width: 25%;
}

.fixed_div {
    height: 100%;
    width: 100%;
    margin-bottom: 2rem;
}

.fixed_div .input_box {
    display: flex;
    justify-content: end;
    align-items: center;
    width: 100%;
    height: 100%;
}

.fixed_div input {
    display: flex;
    margin: .5rem 0;
    padding: .4rem;
    border-radius: 5px;
    border: none;
    outline: none;
}

.fixed_div input:active {
    border: none;
}

.not_found {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    height: 850px;
}

.mySwiper {
    
    padding: 1rem 0;
    border-top: 3px solid var(--color-light-gray);
    border-bottom: 3px solid var(--color-light-gray);
}

.mySwiper .swiper-pagination {
    position: relative;
    inset: 0;
    padding: 1rem 0;
}

.mySwiper .swiper-pagination-bullet-active {
    background: var(--swiper-pagination-color,var(--color-gray))
}

.mySwiper .swiper-button-next, .mySwiper .swiper-button-prev {
    top: 98%;
    z-index: 9999;
    height: 30px;
    color: var(--color-black);
}

.mySwiper .swiper-button-next:after, .mySwiper .swiper-button-prev:after {
    font-size: 1rem;
    font-weight: 900;
}

.app__articlesSwiper .container {
    display: flex;
    flex-direction: column;
    padding: .5rem;
    height: 850px;
    flex: 1;
    border-right: 3px solid var(--color-light-gray);
}

.app__articlesSwiper .container .article_title {
    font-size: 2.8rem;
    line-height: 1.2;
    margin-bottom: 10px;
}

.app__articlesSwiper:nth-child(odd) .container .article_title {
    text-transform: capitalize;
    font-family: 'Lobster', cursive;
    font-weight: 400;
}

.app__articlesSwiper:nth-child(even) .container .article_title {
    text-transform: uppercase;
}
.app__articlesSwiper:nth-child(even) .container .content .article_excerpt{
    -webkit-line-clamp: 9;
    line-clamp: 9;
}

.app__articlesSwiper .container .content .article_excerpt {
    max-height: 350px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 11;
    line-clamp: 11;
    -webkit-box-orient: vertical;
    margin: .5rem 0;
}

.app__articlesSwiper .container .article_date {
    color: var(--color-light-gray);
    font-size: .9rem;
    text-decoration-skip: ink;
    display: flex;
    width: 100%;
    flex-direction: row;
    margin: .5rem 0;
    font-weight: 500;
}

.app__articlesSwiper .container .article_date p {
    width: fit-content;
}

.app__articlesSwiper .container .underline_dec {
    border-bottom: solid 2.5px var(--color-light-gray);
    width: 60%;
}

.app__articlesSwiper .container .read_more {
    background-color: var(--color-gray);
    margin: .5rem 0;
    border-radius: 5px;
    color: var(--color-background);
    padding: .7rem;
    font-size: .9rem;
    font-style: italic;
}

.app__articlesSwiper .container .read_more a {
    text-decoration: none;
    color: var(--color-golden);
    display: flex;
    align-items: flex-end;
}

.app__articlesSwiper .container .article_excerpt {
    font-size: 1rem;
    line-height: 1.4;
}
/* Controling First Article */
.app__articlesSwiper.order_0 .container .header {
    order: 1;
}
.app__articlesSwiper.order_0 .container .feature_img {
    order: 2;
}
.app__articlesSwiper.order_0 .container .content {
    order: 3;
}
/*  */
/* Controling Second Article */
.app__articlesSwiper.order_1 .container .header {
    order: 1;
}
.app__articlesSwiper.order_1 .container .feature_img {
    order: 3;
}
.app__articlesSwiper.order_1 .container .content {
    order: 2;
}
/*  */
/* Controling Third Article */
.app__articlesSwiper.order_2 .container .header {
    order: 2;
}
.app__articlesSwiper.order_2 .container .feature_img {
    order: 1;
}
.app__articlesSwiper.order_2 .container .content {
    order: 3;
    flex-direction: column-reverse;
    display: flex;
}
/*  */
/* Controling Fourth Article */
.app__articlesSwiper.order_3 .container .header {
    order: 1;
}
.app__articlesSwiper.order_3 .container .feature_img {
    order: 3;
}
.app__articlesSwiper.order_3 .container .content {
    order: 2;

}
/*  */
.app__articlesSwiper.order_4 .container .header {
    order: 1;
}
.app__articlesSwiper.order_4 .container .feature_img {
    order: 3;
}
.app__articlesSwiper.order_4 .container .content {
    order: 2;
}

.app__articlesSwiper .container .feature_img {
    width: 100%;
    height: 350px;
    object-fit: cover;
}

.swiper-container {
    width: 100%;
}
  