.app__singlePage {
    width: 100%;
    border-top: 2.5px solid rgb(163, 161, 161);
    border-bottom: 2.5px solid rgb(163, 161, 161);
    padding: 2.5rem 1rem;
    height: 100%;
    margin-bottom: 2.5rem;
}

.app__singlePage-title {
    font-size: 4.5rem;
    text-transform: capitalize;
}

.app__singlePage-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.app__singlePage-image {
    width: 60%;
    height: 100%;
    padding: 1rem;
}

.app__singlePage-image img {
    width: 100%;
    height: 100%;
    outline: 2.5px solid rgb(163, 161, 161);
}

.app__singlePage-content {
    width: 40%;
    height: 100%;
    padding: 0 2.5rem;
}

.app__singlePage-share_buttons {
    display: flex;
    justify-content: end;
    border-bottom: 2.5px solid rgb(163, 161, 161);
    height: 100%;
    width: 100%;
    padding: .5rem;
}

.app__singlePage-content a {
    text-decoration: none;
    display: flex;
    align-items: center;
    background-color: lightblue;
    width: fit-content;
    color: blue;
    padding: .2rem .2rem;
    font-size: .8rem;
}

.app__singlePage-content a .icon {
    margin-right: .3rem;
}

.app__singlePage-content p {
    font-size: 1rem;
    color: var(--color-black);
    line-height: 1.6;
}
